
$body:#000;

.banner{
    background: $body;
    background-image: url(./Components/images/banner.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    height: 700px;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;

    &::after{
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        opacity: 0.4;
        background: $body;
    }
    .button_box{
        width: 50%;
        display: flex;
        flex-direction: column;
        z-index: 10;
        padding-left:2rem ;

        @media only screen and (max-width: 991px){
            &{
                width: 100%; 
            }
        }
        @media only screen and (max-width: 691px){
            &{
                align-items: center; 
                padding-left:0rem ;
            }
        }
        h1,h2{
            margin-bottom: 1rem;
            @media only screen and (max-width: 991px){
                &{
                    word-wrap:break-word; 
                }
            }
            @media only screen and (max-width: 691px){
                &{
                    text-align:center; 
                }
            }
        }
        h1{
            color: #fff;
            font-size: 4rem;
            font-weight: bolder;
            @media only screen and (max-width: 991px){
                &{
                    font-size: 2.3rem;
                }
            }
        }
        h2{
            color: #ffffff96;
            font-size: 1.5rem;
            font-weight: normal;
            @media only screen and (max-width: 991px){
                &{
                    font-size: 1.3rem;
                }
            }
        }
        div{
            display: flex;
            align-items: center;
            @media only screen and (max-width: 691px){
                &{
                    padding-left: 2rem;
                }
            }

            button{
                color: $body;
                width:auto;
                flex-wrap: wrap;
                padding: 10px 15px;
                margin-right: 2rem;
                border-radius: 5px;
                font-weight: bold;
                border: none;
                cursor: pointer;
                transition: all .3s;
                display: flex;
                align-items: center;
                justify-content: center;
                svg{
                    color: #000 !important;
                    padding-right: 4px;
                    font-size: 1.3rem;
                }
                &:hover{
                    background-color: #464545af;
                    color: #fff !important;  
                 svg{
                        color: #fff !important;
                    } 
                }                
                @media only screen and (max-width: 991px){
                    &{
                        width:auto;
                    }
                }

            }
        }
    }
}
.row{
    padding: 1rem;
   h2{
      font-size: 1.6rem;
      margin: 0.3rem;
      color: #fff;
   } 
  .card{
      display: flex;
      overflow-x: auto;
      margin-top: 2rem;

    &::-webkit-scrollbar{
        appearance: none;
    }
  }
.card_img{
        width: 200px;
        cursor: pointer;
        transition: transfrom .5s;
        margin: 0.3rem;
        &:hover{
        transform: translateY(-10px);
        }
    }
}

